// src/index.ts
function getIndent(line) {
  const match = line.match(/^[ \t]+/);
  return match ? match[0] : "";
}
function getFinalIndent(value) {
  const match = value.match(/(?<=(\r\n|\r|\n))[ \t]+$/);
  return match ? match[0] : "";
}
function multiline(value, ...inputs) {
  let currentIndent = "";
  const strings = typeof value === "string" ? value.split(/(?=\r\n|\r|\n)/) : value.map((value2) => value2);
  const lines = strings.map((value2, i) => {
    if (value2.match(/\r\n|\r|\n/)) {
      currentIndent = getFinalIndent(value2) || currentIndent;
    }
    return value2 + (inputs[i] || "").replace(/\r\n|\r|\n/g, "\n" + currentIndent);
  }).join("").split(/\r\n|\r|\n/);
  const minIndent = Math.min(
    ...lines.filter((line) => Boolean(line.match(/[^ \t]/))).map((line) => getIndent(line).length)
  );
  return lines.map((line) => line.slice(minIndent)).join("\n").replace(/^\n|\n[ \t]*$/g, "");
}
export {
  multiline as default
};
